<template>
  <group class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
 <group @click='clickQualitySMD' class="jq-mt">
   <div class="chart" v-ebarline="barLineData"></div>
    </group>
     <group class="jq-mt">
        <div class="chart" v-ebar="barData"></div>
     </group>
</template>
  <script>
    //import {Group,Selector,PopupPicker} from 'vux/src/components'
    import Group from '*/group'
    import Selector from '*/selector'
    import PopupPicker from '*/popup-picker'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Group,
    PopupPicker,
    Selector
    },
    data () {
    return {
    barLineData:{},
    dateTile:'日期',
    time: formatData.defaultTime,
    timeList: formatData.dateList,
    permissions:localStorage.getItem("permissions"),
    barData:{}
    }
    },
    ready(){
      document.title='品质管理';
    },
    methods: {
      clickQualitySMD(){
        this.$route.router.go({name: 'lldesmd'})
      },
    async getData() {
        commit('UPDATE_LOADING', true)
    let id ='';
     if(this.permissions=='201'){//fpi
       id ='APP10104';
      }else if(this.permissions=='203'){
          id ='APP10105';
      }
    let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
    if(dataTit.success){

    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barLineData = {};
    this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    },
     async getBarData() {
         commit('UPDATE_LOADING', true)
    let id ='';
     if(this.permissions=='201'){//fpi
       id ='APP10304a';
      }else if(this.permissions=='203'){
          id ='APP10304b';
      }
    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){

    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barData = {};
    this.barData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
    watch: {
     time (newtime, oldtime) {
      this.getData();
      this.getBarData();
       formatData.setDateTime(newtime);
     }
    },
    created: function () {
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.barData={};
    this.barData.dataset=[];
    this.getData();
    this.getBarData();
    }
    }
  </script>






